.demo-app {
	height: 100%;
	position: absolute;
	width: 100%;
	margin: auto;
	display: table;
}

.demo-container {
	display: table-cell;
  vertical-align: middle;
}

.demo-app .scoreboard-control-panel {
	text-align: center;
	margin-top: 15px;
}
.demo-app .demo-container {
	margin-top: 20px;
	padding: 20px;
	padding-bottom: 5px;
	/* display:flex; */
}
.demo-app .btn-demo {
	border-radius: 3px;
	background-color: #272727;
	margin: 20px 10px 0 10px;
	padding: 5px 10px;
	border: 1px solid rgba(255, 255, 255, 0.1);
	font-size: 1.1rem;
	line-height: 1.1rem;
	font-weight: 300;
